@import 'styles/common';

.About {
  // Figure component variables
  --img-size: 8em;
  --border-radius: 50%;

  @media screen and (min-width: 425px) {
    --img-size: 12em;
  }
  @media screen and (min-width: 768px) {
    --img-size: 24em;
  }
  @media screen and (min-width: 1024px) {
    justify-content: center;
  }
}
